export default {
  data () {
    return {
      // 成人人数
      adultNum: null,
      // 儿童人数
      childrenNum: null,
      // 成人区域的姓名输入框的输入内容
      adultArray: [],
      // 成人区域的身份证输入框的输入内容
      adultArrayId: [],
      // 儿童区域的姓名输入框的输入内容
      childrenArray: [],
      // 儿童区域的身份证输入框的输入内容
      childrenArrayId: [],
      // 预订人信息
      reserve: {
        // 姓名
        reserveUname: "",
        // 手机号
        reserveTel: "",
        // 备注
        reserveRemarks: ""
      },
      // 传递过来的商品的 id
      ProductId: "",
      // 传递过来的 日期
      fulldate: "",
      // 支付凭证
      voucherNumber: "",
      // 订单 id
      orderId: '',
      // 用户的 openid
      openid: '',
      // 支付金额
      MaxShowPrice: ''
    };
  },
  methods: {
    // 提交表单
    formSubmit (e) {
      // console.log(adultArrayStr);
      // console.log(adultArrayIdStr);
      // console.log(childrenArrayStr);
      // console.log(childrenArrayIdStr);
      // 成人姓名
      var adultArrayStr = this.adultArray.join(",");
      // 成人身份证
      var adultArrayIdStr = this.adultArrayId.join(",");
      // 儿童姓名
      var childrenArrayStr = this.childrenArray.join(",");
      // 儿童身份证
      var childrenArrayIdStr = this.childrenArrayId.join(",");
      var unames = adultArrayStr + "," + childrenArrayStr;
      var Ids = adultArrayIdStr + "," + childrenArrayIdStr;
      this.$http
        .post("/api/WeiXinApplet.ashx", {
          action: "SubmmitOrder",
          productId: this.ProductId,
          openDate: this.fulldate,
          UserNames: unames,
          IDCards: Ids,
          Contact: this.reserve.reserveUname,
          ContactPhone: this.reserve.reserveTel,
          Remark: this.reserve.reserveRemarks
        })
        .then(res => {
          // console.log(res)
          if (res.data.status == "true") {
            this.voucherNumber = res.data.voucherNumber;
            // 调用支付接口
            // this.payment()
          }
        });
    },
    // 去确认订单页面
    goConfirmationOrder () {
      if (isNaN(this.adultNum)) {
        var number = this.childrenNum;
      } else if (isNaN(this.childrenNum)) {
        var number = this.adultNum;
      } else {
        var number = this.adultNum + this.childrenNum;
      }
      var total = this.adultArray.length + this.childrenArray.length;
      if (
        total == number &&
        this.reserve.reserveUname !== "" &&
        this.reserve.reserveTel !== ""
      ) {
        this.formSubmit()
      } else {
        uni.showToast({
          title: "请将信息填写完整",
          duration: 2000,
          icon: "none"
        });
      }
    },
    payment () {
      this.$http.post('/api/WeiXinPay.ashx', {
        action: 'Pay',
        voucherNumber: this.voucherNumber,
        OpenId: this.openid
      }).then(res => {
        console.log(res)
        var total_fee = res.data.total_fee
        uni.requestPayment({
          provider: 'wxpay',
          timeStamp: res.data.timeStamp,
          nonceStr: res.data.nonceStr,
          package: 'prepay_id=' + res.data.prepay_id,
          signType: 'MD5',
          paySign: res.data.paySign,
          success: (res) => {
            console.log(res)
            if (res.errMsg == 'requestPayment:ok') {
              uni.reLaunch({
                url: '/pages/paymentResult/paymentResult'
              })
            } else {
              console.log('支付失败')
            }
          },
          fail: (err) => {
            uni.reLaunch({
              url: '/pages/orderList/orderList'
            })
          }
        });
      })
    }
  },
  onLoad (options) {
    this.adultNum = parseInt(options.adultNum);
    this.childrenNum = parseInt(options.childrenNum);
    this.ProductId = options.ProductId;
    this.fulldate = options.fulldate;
    this.openid = uni.getStorageSync('openid')
    console.log(options)
    this.MaxShowPrice = options.MaxShowPrice
  }
};
